import {ActivityItem} from "./ActivitiesItem/ActivityItem";
import style from "./ActivityBlock.module.scss"

export type ActivityBlockType = {
    title: string
}

export const ActivityBlock = (props: ActivityBlockType) => {
    return (
        <div>
            {props.title === 'Experience'
            ?
            (<div className={style.activities}>
                <ActivityItem
                    date={'2018-Present'}
                    placeName={'-'}
                    placeLocation={'.'}
                    description={'   .          ;      ;     ;    ;         ;'}
                />
                <ActivityItem
                    date={'2009-2018'}
                    placeName={'-'}
                    placeLocation={'.'}
                    description={'     ,     .       ,     ;     ;            ;      ;'}
                />
            </div>)
            :
            (<div className={style.activities}>
                <ActivityItem
                    date={'2004-2009'}
                    placeName={'   '}
                    placeLocation={'.'}
                    description={'    ,    ,  - '}
                />
                <ActivityItem
                    date={'2010-2013'}
                    placeName={'   '}
                    placeLocation={'.'}
                    description={'   ,   ,   ,  - '}
                />
            </div>)}
        </div>
    )
}